.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 10;
  background: rgba(0, 0, 0, 0.75);
}

.modal {
  position: fixed;
  top: 30vh;
  left: 10%;
  width: 80%;
  z-index: 100;
  overflow: hidden;
  animation: slide-down 300ms ease-out forwards;
}

.header {
  background: rgb(64, 0, 54);
  padding: 1rem;
}

.header p {
  color: rgb(64, 0, 54);
  font-style: italic;
}

.header h2 {
  margin: 0;
  color: white;
}

.content {
  padding: 1rem;
  color: black;
  font-size: 20px;
  font-weight: 600;
  /* font-style: italic; */
}

.actions {
  padding: 1rem;
  display: flex;
  justify-content: flex-start;
}

.button {
  font: inherit;
  font-size: 20px;
  border: 0.5px solid rgb(64, 0, 54);
  border-radius: 8px;
  background: rgb(64, 0, 54);
  color: white;
  padding: 0.25rem 1rem;
  cursor: pointer;
}

.button:hover,
.button:active {
  background: rgb(158, 1, 135);
  border-color: white;
  color: white;
  /* color: orangered; */
}

.button:focus {
  outline: none;
}

@media (min-width: 768px) {
  .modal {
    left: calc(50% - 20rem);
    /* right: calc(50% - 20rem); */
    width: 40rem;
  }
}

@keyframes slide-down {
  from {
    opacity: 0;
    transform: translateY(-3rem);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
