.grid-container {
  display: grid;
  grid-template-columns: auto auto auto auto auto;
  padding: 2px;
  width: 450px;
  height: 450px;
  margin-left: 37%;
  margin-top: 1%;
  border: 1px solid rgb(255, 0, 217);
  box-shadow: 0px 9px 11px 0px rgb(103, 0, 96);
}
.grid-item {
  background-color: rgb(255, 0, 217);
  border: 1px solid white;
  padding: 20px;
  font-size: 30px;
  text-align: center;
  color: white;
}
@media (max-width: 768px) {
  .grid-container {
    display: grid;
    grid-template-columns: 80px 80px 80px 80px 80px;
    padding: 2px;
    width: 400px;
    height: 400px;
    margin-left: 12%;
    margin-top: 5%;
    border: 1px solid rgb(255, 0, 217);
    box-shadow: 0px 9px 11px 0px rgb(103, 0, 96);
  }
  .grid-item {
    background-color: rgb(255, 0, 217);
    border: 1px solid white;
    padding: 1.5rem 1rem;
    font-size: 22px;
    text-align: center;
    color: white;
    font-weight: 600;
  }
}
